@import "../../App.scss";

.container{
    display: flex;
    width :100vw;
    height : 100vh;
    justify-content: center;
    align-items: center;
    .modal{
        width : 500px;
        min-height : 128px;
        background-color: $DarkGrayishBlue;
        display: flex;
        flex-direction: column;
        padding : 3rem 0.5rem 3.5rem 0.5rem;
        border-radius: 0.5rem;
        position: relative;

        .title{
            text-align: center;
            color : $NeonGreen;
            letter-spacing: 0.25rem;
            font-size : 0.8rem;
            margin-bottom : 0.5rem;
        }

        .advice{
            font-size: 1.5rem;
            color : $LightCyan;
            text-align: center;
            margin : 1.5rem auto 0;
            width : 95%;

        }

        .separetor{
            text-align: center;
            width : 100%;
            margin : 1rem  auto 0.5rem;
        }
    }
}