@import "../../App.scss";

.btn-container
{
    position: absolute;
    bottom : -1.8rem;
    left : 50%;
    transform: translateX(-50%) scale(1.2);
    .btn{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0.5rem;
        border : none;
        background : $NeonGreen;
        width : 60px;
        height: 60px;
        border-radius: 50%;
        cursor: pointer;

        &:active{
            box-shadow: $NeonGreen 5px 5px 50px;
            outline : none
        }
    }
}